@headerHeight:1.1rem;
@footerHeight:1.1rem;
@headerColor:#333;
@footerColor:#333;
@ToastFont:3.5vw;
@ToastHeight:.5rem;
@positon:fixed;
html,body,.m-container,.m-wrap{
    margin: 0;padding: 0;
    height: 100%;
}
.m-header,.m-footer{
    position:@positon;
    background:#fff;
    z-index: 999;
}
.m-header{
    width: 100%;
    height: @headerHeight;
    top: 0;
    left: 0;
}
.m-center{
    background:#f5f5f5;
    height: 100%;
    position: relative;
}
.m-footer{
    width: 100%;
    height: @footerHeight;
    left: 0;bottom: 0;
}

.m-header-button{
    -webkit-box-flex: .5;
    -ms-flex: .5;
    flex: .5;
}
.m-header{
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0 0.3rem;
    text-align: center;
    white-space: nowrap;
    color: @headerColor;
}
.m-header-title{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: inherit;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    font-weight: normal;
    color: #222;
}   
.header-is-left{
    text-align: left;
}
.header-is-right{
    text-align: right;
}
.m-footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    color: @footerColor;
}
.m-center{
    width: 100%;
}
a{
    color: @footerColor;
}
.m-header~.m-center{ 
    border-top: @headerHeight solid transparent;
}
.m-footer~.m-center{
    border-bottom: @footerHeight solid transparent;
}





