.weui_mask {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #eee;
}
.weui_mask_transparent {
  position: fixed;
  z-index: 5001;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.m-mask-transition {
  display: none;
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0);
  transition: background .3s;
}
.m-fade-toggle {
  background: rgba(0, 0, 0, 0.6);
}
.m-actionsheet {
  position: fixed;
  left: 0;
  bottom: 0;
  transform: translate(0, 100%);
  -webkit-transform: translate(0, 100%);
  -moz-transform: translate(0, 100%);
  -o-transform: translate(0, 100%);
  backface-visibility: hidden;
  z-index: 5000;
  width: 100%;
  transition: transform .3s;
  -webkit-transition: transform .3s;
  -moz-transition: transform .3s;
  -o-transition: transform .3s;
}
.m-actionsheet-menu {
  background-color: #FFFFFF;
}
.weui_actionsheet_action {
  margin-top: 6px;
  background-color: #FFFFFF;
}
.m-actionsheet-cell {
  position: relative;
  padding: 10px 0;
  text-align: center;
  font-size: 18px;
}
.m-actionsheet-cell:active {
  background-color: #ECECEC;
}
.m-actionsheet-cell:first-child:before {
  display: none;
}
.m-actionsheet-toggle {
  transform: translate(0, 0);
  -webkit-transform: translate(0, 0);
  -moz-transform: translate(0, 0);
  -o-transform: translate(0, 0);
}
.vux-actionsheet-gap {
  height: 8px;
  width: 100%;
  background-color: #eee;
}
.m-actionsheet-cancel {
  border-radius: 15px;
  margin: 0px 15px;
  margin-bottom: 13px;
}
.m-actionsheet-cancel:before {
  border-top: none;
}
.m-fade-toggle {
  background: rgba(0, 0, 0, 0.3);
}
.m-actionsheet {
  background-color: inherit ;
}
.m-actionsheet-menu {
  background-color: inherit;
}
.m-actionsheet-cell {
  background-color: #fff;
  padding: 15px 0;
}
.m-actionsheet-container {
  margin: 0;
  overflow: hidden;
}



