.m-flex {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.line {
  width: 100%;
  height: 1px;
  background-color: #f5f5f5;
}
.m-swipe-desc {
  text-align: center;
  color: #666;
  margin-bottom: 5px;
}
.m-swipe {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
  margin-bottom: 20px;
}
.m-swipe-item {
  line-height: 200px;
}
.slide1 {
  background-color: #0089dc;
  color: #fff;
}
.slide2 {
  background-color: #ffd705;
  color: #000;
}
.slide3 {
  background-color: #ff2d4b;
  color: #fff;
}
.m-tab {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.m-tab-content {
  width: 100%;
  color: #000;
}



