    min-height: 48px;
    padding-top: 12px;
    padding-bottom: 12px;
    font-size:16px;
    background: none;
    appearance: none;
    text-decoration: none;
    border: none;
    outline: none;
    flex: 1;
    color: #999;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: normal;
    align-items: center;
    cursor: pointer;
}

.m-tab-active {
    color: #333;
}

.m-tab-text {
    &.has-icon {
        margin-top: 8px;
    }
}



