@import '../../less/actionsheet.less';

.vux-actionsheet-gap {
  height: 8px;
  width: 100%;
  background-color: #eee;
}

.m-actionsheet-cancel{
  border-radius: 15px;
  margin:0px 15px;
  margin-bottom:13px;
}

.m-actionsheet-cancel:before {
  border-top: none;
}

// css hack
.m-fade-toggle {
  background: rgba(0, 0, 0, 0.3);
}

.m-actionsheet {
    background-color: inherit ;
}

.m-actionsheet-menu {
    background-color: inherit;
}

.m-actionsheet-cell {
    background-color: #fff;
    padding: 15px 0;
}

.m-actionsheet-container{
  margin:0;
  overflow:hidden
}



