@import url("style/default.css");
@import url("font/iconfont.css");
html,
body,
.m-container,
.m-wrap {
  margin: 0;
  padding: 0;
  height: 100%;
}
.m-header,
.m-footer {
  position: fixed;
  background: #fff;
  z-index: 999;
}
.m-header {
  width: 100%;
  height: 1.1rem;
  top: 0;
  left: 0;
}
.m-center {
  background: #f5f5f5;
  height: 100%;
  position: relative;
}
.m-footer {
  width: 100%;
  height: 1.1rem;
  left: 0;
  bottom: 0;
}
.m-header-button {
  -webkit-box-flex: .5;
  -ms-flex: .5;
  flex: .5;
}
.m-header {
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 0.3rem;
  text-align: center;
  white-space: nowrap;
  color: #333;
}
.m-header-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: inherit;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  font-weight: normal;
  color: #222;
}
.header-is-left {
  text-align: left;
}
.header-is-right {
  text-align: right;
}
.m-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  color: #333;
}
.m-center {
  width: 100%;
}
a {
  color: #333;
}
.m-header ~ .m-center {
  border-top: 1.1rem solid transparent;
}
.m-footer ~ .m-center {
  border-bottom: 1.1rem solid transparent;
}
.m-wrap {
  position: relative;
}
.m-header {
  margin-top: env(safe-area-inset-top);
}
[v-cloak] {
  display: none;
}
.m-header-title {
  font-size: 4.42vw;
}
body {
  padding-top: env(safe-area-inset-top);
}



