  overflow: hidden;
  position: relative;
  height: 100%;

  .m-swipe-items-wrap {
    position: relative;
    overflow: hidden;
    height: 100%;

    > div {
      position: absolute;
      transform: translateX(-100%);
      width:100%;
      height: 100%;
      display: none;
    }
    .is-active {
        display: block;
        transform: none;
      }
  }

  .m-swipe-indicators {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    .is-active {
      background: #fff;
    }
  }

  .m-swipe-indicator {
    width: 8px;
    height: 8px;
    display: inline-block;
    border-radius: 100%;
    background: #000;
    opacity: 0.2;
    margin: 0 3px;
  }
}



