







































@color-grey: #d9d9d9;
@color-white: #fff;
@color-blue: #26a2ff;
.m-switch {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;

    * {
        pointer-events: none;
    }

    .m-switch-label {
        margin-left: 10px;
        display: inline-block;

        &:empty {
            margin-left: 0;
        }
    }

    .m-switch-core {
        display: inline-block;
        position: relative;
        width: 52px;
        height: 32px;
        border: 1px solid @color-grey;
        border-radius: 16px;
        box-sizing: border-box;
        background: @color-grey;

        &::after {
            content: " ";
            position: absolute;
            -webkit-transition: -webkit-transform .3s;
            transition: -webkit-transform .3s;
            transition: transform .3s;
            transition: transform .3s, -webkit-transform .3s;
            border-radius: 15px;
            width: 30px;
            height: 30px;
            background-color: @color-white;
            box-shadow: 0 1px 3px rgba(0, 0, 0, .4);
        }

        &::before {
            content: " ";
            position: absolute;
            -webkit-transition: -webkit-transform .3s;
            transition: -webkit-transform .3s;
            transition: transform .3s;
            transition: transform .3s, -webkit-transform .3s;
            border-radius: 15px;
            width: 50px;
            height: 30px;
            background-color: #fdfdfd;
        }
    }

    .m-switch-input {
        display: none;

        &:checked {
            +.m-switch-core {
                border-color: @color-blue;
                background-color: @color-blue;

                &::before {
                    transform: scale(0);
                }

                &::after {
                    transform: translateX(20px);
                }
            }
        }
    }
}



